<template>
    <div class="alarm">
        <Topbar></Topbar>
        <mu-card v-for="item,index in alarmList">
          <mu-card-header :title="'设备名称:'+item.groupName" :subTitle="'系统:'+item.systemName+',地点:'+item.locationName+',房间:'+item.roomName">
            <!-- <mu-avatar src="/images/uicon.jpg" slot="avatar"/> -->
          </mu-card-header>
          <!-- <mu-card-title title="Content Title" subTitle="Content Title"/> -->
          <div class="alarm-text">
          <span v-if="item.show == null || item.show == false">
            <b>报警日期:&nbsp;&nbsp;{{item.startTime | dateAll}}-{{item.endTime | dateAll}}</b><br/>
            <b>报警信息:</b>{{item.describes.substring(0,150)}}<a class="alarm-more" @click="showMore(item,index)">&nbsp;&nbsp;>>详情&nbsp;&nbsp;</a>
          </span>
          <span v-if="item.show== true">
            <b>报警日期:&nbsp;&nbsp;{{item.startTime | dateAll}}-{{item.endTime | dateAll}}</b><br/>
            <b>报警信息:</b>{{item.describes}}<a class="alarm-more" @click="showMore(item,index)">&nbsp;&nbsp;<<收起&nbsp;&nbsp;</a>
          </span>
          </div>
          <mu-card-actions>
            <mu-flat-button v-if="item.confirm == 1" icon="done" label="已确认" primary/>
            <mu-flat-button v-if="item.confirm == 0" icon="search" label="未确认" primary/>
          </mu-card-actions>
        </mu-card>
        <Tabbar></Tabbar>
    </div>
</template>

<script>
import Tabbar from 'components/Tabbar';
import Topbar from 'components/Topbar';
import { MessageBox, Indicator } from 'mint-ui';
import * as type from './../../store/base/type';

export default{
  components: {
    Tabbar,
    Topbar,
  },
  data() {
    return {
      alarmList: null,
      args: {
        currentPage: 1,
        numberPerPage: 20,
      },
    };
  },
  created() {
    this.getAlarmList();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    getAlarmList() {
      Indicator.open();
      const self = this;
      this.$store.dispatch(type.ALARM_LIST, this.args).then((data) => {
        console.log(data);
        self.alarmList = data;
        Indicator.close();
      }).catch((err) => {
        Indicator.close();
        MessageBox('消息', err);
      });
    },
    showMore(item, index) {
      item.show = !item.show;
      this.$set(this.alarmList, index, item);
      console.log(item.show);
    },
  },
};
</script>
<style lang="css" scoped>
    .alarm{
        text-align: left;
        margin-top: 60px;
        width: 100%;
    }
    .alarm-text {
      margin: 0px 15px 0px 15px;
    }
    .alarm-text span{
      display: inline-block;
      /* text-decoration: none; */
      /* word-wrap: break-word; */
      word-break: break-all;
    }
    .alarm-more {
      color: black;
      font-weight: bold;
    }
</style>



